<template>
  <div class='post-page'>
    <el-card>
      <div slot="header">
        <my-bread>{{$route.query.id?'':''}}</my-bread>
      </div>
      <!--  -->
      <el-form ref="articleForm" :model="articleForm" :rules="articleRules" label-width="120px">
        <el-form-item label="" prop="title">
          <el-input v-model="articleForm.title" placeholder="" style="width:400px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <quill-editor @blur="checkContent()" v-model="articleForm.content" :options="editorOption"/>
        </el-form-item>
        <el-form-item label="" prop="cover">
          <el-radio-group @change="articleForm.cover.images=[]" v-model="articleForm.cover.type">
            <el-radio :label="1"></el-radio>
            <el-radio :label="3"></el-radio>
            <el-radio :label="0"></el-radio>
            <el-radio :label="-1"></el-radio>
          </el-radio-group>
          <!-- my-cover -->
          <div v-if="articleForm.cover.type===1||articleForm.cover.type===3">
            <my-cover
              @on-confirm="checkCover()"
              v-for="i in articleForm.cover.type"
              :key="i"
              v-model="articleForm.cover.images[i-1]">
            </my-cover>
          </div>
        </el-form-item>
        <el-form-item label="" prop="channel_id">
          <my-channel v-model="articleForm.channel_id"></my-channel>
        </el-form-item>
        <el-form-item label="" v-if="$route.query.id">
          <el-button type="success" @click="updateArtcile()"></el-button>
        </el-form-item>
        <el-form-item label="" v-else>
          <el-button type="primary" @click="addArticle(false)"></el-button>
          <el-button  @click="addArticle(true)"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'

import { quillEditor } from 'vue-quill-editor'
export default {
  name: 'PostPage',
  components: { quillEditor },
  data () {
    // 
    const checkCoverFn = (rule, value, cb) => {
      // value
      if (value.type === 1) {
        if (!value.images[0]) {
          return cb(new Error(''))
        }
      }
      if (value.type === 3) {
        if (!(value.images[0] && value.images[1] && value.images[2])) {
          return cb(new Error(''))
        }
      }
      // 
      cb()
    }
    return {
      // 
      articleRules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 50, message: '3-50', trigger: 'blur' }
        ],
        content: [
          { required: true, message: '', trigger: 'blur' }
        ],
        channel_id: [
          { required: true, message: '', trigger: 'change' }
        ],
        cover: [
          { validator: checkCoverFn, trigger: 'change' }
        ]
      },
      // 
      articleForm: {
        title: null,
        content: null,
        cover: {
          type: 1,
          images: []
        },
        channel_id: null
      },
      // 
      editorOption: {
        placeholder: '',
        modules: {
          toolbar: [
            ['bold', 'italic', 'underline', 'strike'],
            ['blockquote', 'code-block'],
            [{ header: 1 }, { header: 2 }],
            [{ list: 'ordered' }, { list: 'bullet' }],
            [{ indent: '-1' }, { indent: '+1' }],
            ['image']
          ]
        }
      }
    }
  },
  watch: {
    '$route.query.id': function () {
      if (this.$route.query.id) {
        // 
        this.getArticle()
      } else {
        // 
        this.$refs.articleForm.resetFields()
      }
    }
  },
  created () {
    // 
    if (this.$route.query.id) {
      this.getArticle()
    }
  },
  methods: {
    // 
    async getArticle () {
      const { data: { data } } = await this.$http.get(`/mp/v1_0/articles/${this.$route.query.id}`)
      this.articleForm = data
    },
    checkContent () {
      // 
      this.$refs.articleForm.validateField('content')
    },
    checkCover () {
      // 
      this.$refs.articleForm.validateField('cover')
    },
    addArticle (draft) {
      // 
      this.$refs.articleForm.validate(async (valid) => {
        if (valid) {
          // 
          try {
            await this.$http.post(`/mp/v1_0/articles?draft=${draft}`, this.articleForm)
            // +
            this.$message.success(draft ? '' : '')
            this.$router.push('/articles')
          } catch (e) {
            this.$message.error(draft ? '' : '')
          }
        }
      })
    },
    updateArtcile () {
      this.$refs.articleForm.validate(async (valid) => {
        if (valid) {
          // 
          try {
            // 
            await this.$http.put(`/mp/v1_0/articles/${this.$route.query.id}`, this.articleForm)
            this.$message.success('')
            this.$router.push('/articles')
          } catch (e) {
            this.$message.success('')
          }
        }
      })
    }
  }
}
</script>

<style scoped lang='less'>
::v-deep .ql-toolbar.ql-snow {
  padding: 0 8px;
}
::v-deep .ql-editor {
  height: 200px;
}
</style>
